import React from 'react';
import PageNumber from '../Components/PageNumber';
import REPlusRedLogo from '../Components/REPlusRedLogo';
import HeadingBlackLeft1 from '../Components/Headings/HeadingBlackLeft1';

const Page16 = () => (
  <div className="page_content">
    {/* <img
      alt=""
      src={require("../Images/16/orig.png")}
      style={{
        top: 0,
        left: 0,
        opacity: 0.3
      }}
    /> */}

    <HeadingBlackLeft1 />
    <REPlusRedLogo>
      <span style={{
        fontSize: 100,
        fontWeight: 'bolder',
        color: '#e60013',
        letterSpacing: -5,
        whiteSpace: 'nowrap'
      }}>
        BEAUTIFUL WALL
      </span>
    </REPlusRedLogo>

    <div style={{
      top: 240,
      left: 131,
      width: 993,
      lineHeight: 2,
      fontSize: 17,
      fontWeight: 'bold',
      letterSpacing: 1
    }}>
      紙ハニカム中間層がサンドイッチ構造で形成された、40mm厚の壁面システム。<br />
      環境に優しく、輸送コストも抑えられる軽量構造です。組み立てが容易で、<br />
      業界トップクラスの高画質グラフィック + カッティング貼り付けを可能にした、美しい壁面を実現します。<br />
    </div>

    <img
      alt=""
      src={require("../Images/16/02.png")}
      style={{
        top: 375,
        left: 131
      }}
    />
    <img
      alt=""
      src={require("../Images/16/03.png")}
      style={{
        top: 1205,
        left: 131
      }}
    />
    <img
      alt=""
      src={require("../Images/16/04.png")}
      style={{
        top: 1205,
        left: 692
      }}
    />
    <PageNumber isRight={false}>16</PageNumber>
  </div>
);

export default Page16;